import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

class Edit extends Component {

    constructor(props) {
        super(props);
        this.state = {
            user: {}
        };
    }

    componentDidMount() {
        axios.get('/users/'+this.props.match.params.id)
            .then(res => {
            this.setState({ user: res.data });
        console.log(this.state.user);
    });
    }

    onChange = (e) => {
    const state = this.state.user
    state[e.target.name] = e.target.value;
    this.setState({user:state});
}

onSubmit = (e) => {
    e.preventDefault();

    const { firstName, lastName, userName, passWord, status } = this.state.user;

    axios.put('/users/'+this.props.match.params.id, { firstName, lastName, userName, passWord, status })
        .then((result) => {
        this.props.history.push("/show/"+this.props.match.params.id)
    });
}

render() {
    return (
        <div class="container">
        <div class="panel panel-default">
        <div class="panel-heading">
        <h3 class="panel-title">
        Edit User
    </h3>
    </div>
    <div class="panel-body">
        <h4><Link to={`/show/${this.state.user.id}`}><span class="glyphicon glyphicon-eye-open" aria-hidden="true"></span> User List</Link></h4>
    <form onSubmit={this.onSubmit}>


        <div class="form-group">
        <label for="name">FirstName:</label>
    <input type="text" class="form-control" name="firstName" value={this.state.user.firstName} onChange={this.onChange} placeholder="First Name" />
        </div>
        <div class="form-group">
        <label for="name">LastName:</label>
    <input type="text" class="form-control" name="lastName" value={this.state.user.lastName} onChange={this.onChange} placeholder="Last Name" />
        </div>
        <div class="form-group">
        <label for="name">UserName:</label>
    <input type="text" class="form-control" name="userName" value={this.state.user.userName} onChange={this.onChange} placeholder="User Name" />
        </div>
        <div class="form-group">
        <label for="name">Password:</label>
    <input type="text" class="form-control" name="passWord" value={this.state.user.passWord} onChange={this.onChange} placeholder="Password" />
        </div>
        <div class="form-group">
        <label for="name">Status:</label>
    <input type="text" class="form-control" name="status" value={this.state.user.status} onChange={this.onChange} placeholder="Status" />
        </div>


        <button type="submit" class="btn btn-success">Update</button>
        </form>
        </div>
        </div>
        </div>
);
}
}

export default Edit;
